import React from 'react';
import p5 from 'p5';
import './chaosGameStyles.css';
import createSketch from './sketch.js';
import Footer from './footerControls';

class ChaosGame extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();

    this.handleChange = this.handleChange.bind(this);
    this.handleScreenChange = this.handleScreenChange.bind(this);

    this.state = {
      height: 0,
      width: 0,
      screenChange: this.handleScreenChange,
      resized: false,
      data: { stopLength: 2, vertexCount: 3, speed: 500, opacity: 125, activeType: ['Normal', 0] },
    };
  }

  componentDidMount() {
    const height = document.getElementById('p5js-container').clientHeight;
    const width = document.getElementById('p5js-container').clientWidth;

    this.setState({ height, width });

    this.myP5 = new p5(createSketch(this.state), this.myRef.current);
  }

  componentWillUnmount() {
    this.myP5.stop();

    this.myP5.remove();
    this.myP5 = undefined;
  }

  handleChange(data) {
    this.setState({ data });
    this.myP5.resetSketch();
  }

  handleScreenChange(resized) {
    const height = document.getElementById('p5js-container').clientHeight;
    const width = document.getElementById('p5js-container').clientWidth;

    this.setState({ height, width, resized });

    return this.state;
  }

  render() {
    return (
      <section key={2} className="sectionClass">
        <div ref={this.myRef} className="p5js" id="p5js-container"></div>
        <Footer
          data={this.state.data}
          start={() => {
            this.myP5.start();
          }}
          stop={() => {
            this.myP5.stop();
          }}
          changeEvent={this.handleChange}
        />
      </section>
    );
  }
}

export default ChaosGame;
