/* eslint-disable no-loop-func */

import { Vector } from 'p5';

function createSketch(props) {
  return (p) => {
    p.windowResized = () => {
      props = props.screenChange(true);
      p.resizeCanvas(props.width, props.height);

      createVertexes(p, vertexCount);
      createInitialPoint(p);
    };

    p.setup = () => {
      p.createCanvas(props.width, props.height);
      p.resetSketch();
    };

    p.draw = () => {
      // For some reason causes the sketch to display correctly after being unmounted
      // TODO: FIGURE OUT WHY!!!
      if (!props.resized) {
        props = props.screenChange(true);
        p.resizeCanvas(props.width, props.height);

        createVertexes(p, vertexCount);
        createInitialPoint(p);
      }

      p.noLoop();
    };

    p.start = () => {
      if (!isRunning) {
        p.background(0);
        createVertexes(p, vertexCount);
        createInitialPoint(p);

        const chaosIndex = props.data.activeType[1];

        switch (chaosIndex) {
          case 0:
            runChaosNormal(p);
            break;

          case 1:
            runChaosNoRepeat(p);
            break;

          case 2:
            runChaosLeftAndRight(p);
            break;

          case 3:
            runChaosOppisiteLeftAndRight(p);
            break;

          case 4:
            runChaosNotLeft(p);
            break;

          case 5:
            runChaosNotDouble(p);
            break;

          default:
            runChaosNormal(p);
            break;
        }
      }
    };

    p.resetSketch = () => {
      if (isRunning) p.stop();

      stopLength = props.data.stopLength;
      vertexCount = props.data.vertexCount;
      speed = props.data.speed;
      opacity = props.data.opacity;

      p.background(0);
    };

    p.stop = () => {
      doStop = true;
    };
  };
}

export default createSketch;

// Stores the vertex points
let vertexArr = [];
// Keeps track of the current point
let currentPoint = undefined;
// Checks whether a chaos algorithm is running
let isRunning = false;
// Stops a running algorithm
let doStop = false;
// Stop length
let stopLength = 2;
// The total vertex count
let vertexCount = 3;
// The speed at which the simulation runs
let speed = 5000;
// Determines the opacity of each dot
let opacity = 50;

function createVertexes(p, count) {
  vertexArr = [];

  const edgeLength = p.height / (count - count / 2);

  vertexArr.push(p.createVector(p.width / 2 - edgeLength / 2, p.height / 6));

  let currentAngle = 0;
  for (let index = 0; index < count - 1; index++) {
    const lastVertex = vertexArr[vertexArr.length - 1].copy();

    if (index === 0) {
      const newVertex = Vector.fromAngle(currentAngle);
      newVertex.setMag(edgeLength);

      newVertex.x += lastVertex.x;
      newVertex.y += lastVertex.y;

      vertexArr.push(newVertex);
    } else {
      const newVertex = Vector.fromAngle(p.radians(currentAngle));
      // newVertex.rotate(p.radians(180 - currentAngle));

      newVertex.setMag(edgeLength);

      newVertex.x += lastVertex.x;
      newVertex.y += lastVertex.y;

      vertexArr.push(newVertex);
    }

    currentAngle += 180 - (180 * (count - 2)) / count;
  }

  p.stroke(247, 37, 133, Number(opacity));
  p.noFill();
}

// Determines a random points within the vertex
function createInitialPoint(p) {
  let i = 0;
  do {
    let intersections = 0;
    const xPos = p.random(p.width);
    const yPos = p.random(p.height);
    // Runs through all vertexes
    for (let index = 0; index < vertexArr.length - 1; index++) {
      const tempIntersect = checkIntersection(
        p.createVector(xPos, yPos),
        p.createVector(xPos * p.width, yPos),
        vertexArr[index].copy(),
        vertexArr[index + 1].copy()
      );

      if (tempIntersect) intersections += 1;
    }
    // Caclulates last vertex and first vertex
    const tempIntersect = checkIntersection(
      p.createVector(xPos, yPos),
      p.createVector(xPos * p.width, yPos),
      vertexArr[vertexArr.length - 1].copy(),
      vertexArr[0].copy()
    );

    if (tempIntersect) intersections += 1;

    if (intersections % 2 !== 0) {
      i += 1;
      currentPoint = p.createVector(xPos, yPos);
    }
  } while (i < 1);
}

// Picks a random vertex and moves halfway, then repeats
async function runChaosNormal(p) {
  isRunning = true;

  p.strokeWeight(0.001);

  do {
    if (doStop) {
      doStop = false;
      break;
    }

    for (let index = 0; index < speed; index++) {
      const randomVertex = p.floor(p.random(vertexArr.length));
      const vertexPoint = vertexArr[randomVertex].copy();

      const newPoint = Vector.sub(currentPoint, vertexPoint);
      newPoint.setMag(newPoint.mag() / stopLength);
      newPoint.x += vertexPoint.x;
      newPoint.y += vertexPoint.y;

      p.point(newPoint.x, newPoint.y);

      currentPoint = newPoint.copy();
    }

    await new Promise((resolve) => setTimeout(resolve, 10));
    console.log('running!');
  } while (true);

  isRunning = false;
}

// Does exatcly what the previous algorithm does, except the same vertex can't be chosen twice
// Keeps track of the last vertex
let lastVertex = undefined;

async function runChaosNoRepeat(p) {
  isRunning = true;

  p.strokeWeight(0.001);

  do {
    if (doStop) {
      doStop = false;
      break;
    }

    for (let index = 0; index < speed; index++) {
      let randomVertex = p.floor(p.random(vertexArr.length));

      while (randomVertex === lastVertex) {
        randomVertex = p.floor(p.random(vertexArr.length));
      }

      lastVertex = randomVertex;
      const vertexPoint = vertexArr[randomVertex].copy();

      const newPoint = Vector.sub(currentPoint, vertexPoint);
      newPoint.setMag(newPoint.mag() / stopLength);
      newPoint.x += vertexPoint.x;
      newPoint.y += vertexPoint.y;

      p.point(newPoint.x, newPoint.y);

      currentPoint = newPoint.copy();
    }

    if (doStop) {
      doStop = false;
      break;
    }

    await new Promise((resolve) => setTimeout(resolve, 10));
  } while (true);

  isRunning = false;
}

async function runChaosLeftAndRight(p) {
  isRunning = true;

  p.strokeWeight(0.001);

  do {
    if (doStop) {
      doStop = false;
      break;
    }

    for (let index = 0; index < speed; index++) {
      let randomVertex;
      if (lastVertex === undefined) {
        randomVertex = p.floor(p.random(vertexArr.length));
      } else {
        const rnd = p.floor(p.random(3));
        if (rnd === 0) lastVertex += 0;
        else if (rnd === 1) lastVertex += 1;
        else if (rnd === 2) lastVertex += -1;
        else throw Error();

        if (lastVertex < 0) lastVertex = vertexArr.length - 1;
        else if (lastVertex >= vertexArr.length) lastVertex = 0;

        randomVertex = lastVertex;
      }

      lastVertex = randomVertex;
      const vertexPoint = vertexArr[randomVertex].copy();

      const newPoint = Vector.sub(currentPoint, vertexPoint);
      newPoint.setMag(newPoint.mag() / stopLength);
      newPoint.x += vertexPoint.x;
      newPoint.y += vertexPoint.y;

      p.point(newPoint.x, newPoint.y);

      currentPoint = newPoint.copy();
    }

    if (doStop) {
      doStop = false;
      break;
    }

    await new Promise((resolve) => setTimeout(resolve, 10));
  } while (true);

  isRunning = false;
}

async function runChaosOppisiteLeftAndRight(p) {
  isRunning = true;

  p.strokeWeight(0.001);

  do {
    if (doStop) {
      doStop = false;
      break;
    }

    for (let index = 0; index < speed; index++) {
      let randomVertex;

      if (lastVertex === undefined) {
        randomVertex = p.floor(p.random(vertexArr.length));
      } else {
        let oppositeVertex = p.floor(lastVertex + vertexArr.length / 2);
        if (oppositeVertex >= vertexArr.length) oppositeVertex -= vertexArr.length;

        const rnd = p.floor(p.random(3));
        if (rnd === 0) oppositeVertex += 0;
        else if (rnd === 1) oppositeVertex += 1;
        else if (rnd === 2) oppositeVertex += -1;
        else throw Error();

        if (oppositeVertex < 0) oppositeVertex = vertexArr.length - 1;
        else if (oppositeVertex >= vertexArr.length) oppositeVertex = 0;

        randomVertex = oppositeVertex;
      }

      lastVertex = randomVertex;
      const vertexPoint = vertexArr[randomVertex].copy();

      const newPoint = Vector.sub(currentPoint, vertexPoint);
      newPoint.setMag(newPoint.mag() / stopLength);
      newPoint.x += vertexPoint.x;
      newPoint.y += vertexPoint.y;

      p.point(newPoint.x, newPoint.y);

      currentPoint = newPoint.copy();
    }

    if (doStop) {
      doStop = false;
      break;
    }

    await new Promise((resolve) => setTimeout(resolve, 10));
  } while (true);

  isRunning = false;
}

async function runChaosNotLeft(p) {
  isRunning = true;

  p.strokeWeight(0.001);

  do {
    if (doStop) {
      doStop = false;
      break;
    }

    for (let index = 0; index < speed; index++) {
      let randomVertex = p.floor(p.random(vertexArr.length));

      if (lastVertex !== undefined) {
        let illegalVertex = lastVertex - 1;
        if (illegalVertex < 0) illegalVertex = vertexArr.length - 1;

        while (illegalVertex === randomVertex) {
          randomVertex = p.floor(p.random(vertexArr.length));
        }
      }

      lastVertex = randomVertex;
      const vertexPoint = vertexArr[lastVertex].copy();

      const newPoint = Vector.sub(currentPoint, vertexPoint);
      newPoint.setMag(newPoint.mag() / stopLength);
      newPoint.x += vertexPoint.x;
      newPoint.y += vertexPoint.y;

      p.point(newPoint.x, newPoint.y);

      currentPoint = newPoint.copy();
    }

    await new Promise((resolve) => setTimeout(resolve, 10));
  } while (true);

  isRunning = false;
}

// For not double only
let secondLastVertex = undefined;

async function runChaosNotDouble(p) {
  isRunning = true;

  p.strokeWeight(0.001);

  do {
    if (doStop) {
      doStop = false;
      break;
    }

    for (let index = 0; index < speed; index++) {
      let randomVertex = p.floor(p.random(vertexArr.length));

      if (secondLastVertex !== undefined) {
        if (secondLastVertex === lastVertex) {
          let leftVertex = lastVertex - 1;
          let rightVertex = lastVertex + 1;

          if (leftVertex < 0) leftVertex = vertexArr.length - 1;
          if (rightVertex >= vertexArr.length) rightVertex = 0;

          let counter = 0;
          while (randomVertex === rightVertex || randomVertex === leftVertex) {
            randomVertex = p.floor(p.random(vertexArr.length));

            counter++;
            if (counter >= 1000) {
              secondLastVertex = undefined;
              lastVertex = undefined;
              return;
            }
          }
        }
      }

      secondLastVertex = lastVertex;
      lastVertex = randomVertex;
      const vertexPoint = vertexArr[lastVertex].copy();

      const newPoint = Vector.sub(currentPoint, vertexPoint);
      newPoint.setMag(newPoint.mag() / stopLength);
      newPoint.x += vertexPoint.x;
      newPoint.y += vertexPoint.y;

      p.point(newPoint.x, newPoint.y);

      currentPoint = newPoint.copy();
    }

    await new Promise((resolve) => setTimeout(resolve, 10));
  } while (true);

  isRunning = false;
}

// Check for intersection
function checkIntersection(p1, p2, p3, p4) {
  // https://www.geeksforgeeks.org/check-if-two-given-line-segments-intersect/
  const orientation = function (p, q, r) {
    const val = (q.y - p.y) * (r.x - q.x) - (q.x - p.x) * (r.y - q.y);

    return val > 0 ? true : false; // clock or counterclock wise
  };

  const o1 = orientation(p1, p2, p3);
  const o2 = orientation(p1, p2, p4);
  const o3 = orientation(p3, p4, p1);
  const o4 = orientation(p3, p4, p2);

  return o1 !== o2 && o3 !== o4;
}
