import { Range, DropDownButton, CheckBox, FooterTab, Hint, Button, FirstClicker, LastClicker } from '../../Controls/Controls';

function Footer(props) {
  const data = props.data;

  const activeSortType = data.sortType;
  const activeDrawType = data.drawType;

  // Handles setting the new sorting, by hard coding the input to the selection data type
  // TODO: Try to find out why??? Not really important it works...
  const clicked = (target, newVal) => {
    const newData = props.data;
    newData[target] = newVal;

    props.changeEvent(newData);
  };

  return (
    <FooterTab>
      <Button>
        <FirstClicker text="Shuffle" onClick={props.startShuffling} />
        <LastClicker text="sort" onClick={props.startSorting} />
      </Button>

      <Hint text="Try hovering over these ->" />

      <Range min="10" max="500" step="10" data={data} changeEvent={props.changeEvent} targetVal="count" text={`Count: ${data.count} points`} />
      <Range min="10" max="1000" step="10" data={data} changeEvent={props.speedChange} targetVal="speed" text={`Speed: ${data.speed} ms`} />

      <DropDownButton changeEvent={props.changeEvent} data={data} prefHeight="20vh" active={data.sortType[0]}>
        <li
          onClick={() => {
            // Sets the new sorting type along with and index for identifying the sorting type
            clicked('sortType', ['Selection Sort', 0]);
          }}
          className={activeSortType[1] === 0 ? 'active' : ''}
        >
          Selection Sort
        </li>
        <li
          onClick={() => {
            clicked('sortType', ['Insertion Sort', 1]);
          }}
          className={activeSortType[1] === 1 ? 'active' : ''}
        >
          Insertion Sort
        </li>
        <li
          onClick={() => {
            clicked('sortType', ['Bubble Sort', 2]);
          }}
          className={activeSortType[1] === 2 ? 'active' : ''}
        >
          Bubble Sort
        </li>
        <li
          onClick={() => {
            clicked('sortType', ['Merge Sort', 3]);
          }}
          className={activeSortType[1] === 3 ? 'active' : ''}
        >
          Merge Sort
        </li>
        <li
          onClick={() => {
            clicked('sortType', ['Quick Sort', 4]);
          }}
          className={activeSortType[1] === 4 ? 'active' : ''}
        >
          Quick Sort
        </li>
        <li
          onClick={() => {
            clicked('sortType', ['Bucket Sort', 5]);
          }}
          className={activeSortType[1] === 5 ? 'active' : ''}
        >
          Bucket Sort
        </li>
      </DropDownButton>

      <DropDownButton changeEvent={props.changeEvent} data={data} prefHeight="20vh" active={data.drawType[0]}>
        <li
          onClick={() => {
            // Sets the new sorting type along with and index for identifying the sorting type
            clicked('drawType', ['Normal Drawmode', 0]);
          }}
          className={activeDrawType[1] === 0 ? 'active' : ''}
        >
          Normal drawmode
        </li>
        <li
          onClick={() => {
            clicked('drawType', ['Center Drawmode', 1]);
          }}
          className={activeDrawType[1] === 1 ? 'active' : ''}
        >
          Center Drawmode
        </li>
        <li
          onClick={() => {
            clicked('drawType', ['Angle Drawmode', 2]);
          }}
          className={activeDrawType[1] === 2 ? 'active' : ''}
        >
          Angle Drawmode
        </li>
        <li
          onClick={() => {
            clicked('drawType', ['Line Drawmode', 3]);
          }}
          className={activeDrawType[1] === 3 ? 'active' : ''}
        >
          Line Drawmode
        </li>
      </DropDownButton>

      <CheckBox changeEvent={props.speedChange} data={data} targetVal="reverseSort" text="Reverse Sort" />
      <CheckBox changeEvent={props.changeEvent} data={data} targetVal="fastSort" text="Fast sort" />
    </FooterTab>
  );
}

export default Footer;
