import { FooterTab, Button, FirstClicker, LastClicker, Range, DropDownButton, Hint } from '../../Controls/Controls';

function Footer(props) {
  const data = props.data;

  const activeType = data.activeType;

  const dropdownClicked = (targetVal, newVal) => {
    const newData = props.data;
    newData[targetVal] = newVal;

    props.changeEvent(newData);
  };

  return (
    <FooterTab>
      <Button>
        <FirstClicker text="Start" onClick={props.start} />
        <LastClicker text="Stop" onClick={props.stop} />
      </Button>

      <Hint text="Hover over these ->" />

      <DropDownButton changeEvent={props.changeEvent} data={data} prefHeight="30vh" active={`Mode: ${data.activeType[0]}`}>
        <li
          onClick={() => {
            dropdownClicked('activeType', ['Normal', 0]);
          }}
          className={activeType[1] === 0 ? 'active' : ''}
        >
          Normal
        </li>

        <li
          onClick={() => {
            dropdownClicked('activeType', ['No repeat', 1]);
          }}
          className={activeType[1] === 1 ? 'active' : ''}
        >
          No repeat
        </li>

        <li
          onClick={() => {
            dropdownClicked('activeType', ['Neighbours only', 2]);
          }}
          className={activeType[1] === 2 ? 'active' : ''}
        >
          Neighbours only
        </li>

        <li
          onClick={() => {
            dropdownClicked('activeType', ['Opposite neighbours only', 3]);
          }}
          className={activeType[1] === 3 ? 'active' : ''}
        >
          Opposite neighbours only
        </li>

        <li
          onClick={() => {
            dropdownClicked('activeType', ['Not anti-clockwise', 4]);
          }}
          className={activeType[1] === 4 ? 'active' : ''}
        >
          Not anti-clockwise
        </li>

        <li
          onClick={() => {
            dropdownClicked('activeType', ['Not double repeat', 5]);
          }}
          className={activeType[1] === 5 ? 'active' : ''}
        >
          Not double repeat
        </li>
      </DropDownButton>

      <Range min="3" max="20" step="1" changeEvent={props.changeEvent} data={data} targetVal="vertexCount" text={`Vertex Count: ${data.vertexCount} points`} />

      <Range min="2" max="5" step="1" changeEvent={props.changeEvent} data={data} targetVal="stopLength" text={`Step length: 1/${data.stopLength} step`} />

      <Range min="100" max="2000" step="100" changeEvent={props.changeEvent} data={data} targetVal="speed" text={`Speed: ${data.speed} dots per cycle`} />

      <Range min="20" max="255" step="1" changeEvent={props.changeEvent} data={data} targetVal="opacity" text={`Opacity: ${data.opacity} / 255`} />

      <Hint text="Some mode's wont generate on lower vertex counts" />
    </FooterTab>
  );
}

export default Footer;
